html, body, #container, .app {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

header {
    -webkit-flex: 1 1;
            flex: 1 1;
}

body, p, h1, h2, h3, h4 {
    margin: 0;
    padding: 0;
    color: #333333;
    font-weight: 500;
}

h1 {
    line-height: 50px;
    margin: 0 0 10px;
    min-height: 80px;
}

h2 {
    margin-bottom: 15px;
    margin-top: 35px;
    font-size: 26px;
    line-height: 25px;
    min-height: 30px;
}

h3 {
    margin-bottom: 10px;
    margin-top: 20px;
    line-height: 25px;
    min-height: 30px;
}

p {
    line-height: 25px;
    padding: 15px 0;
}

a {
    color: #333333;
    text-decoration: none;
}

p a, li a {
    color: #3072C4;
}

p a:hover, li a:hover {
    color: #3072C4;
    text-decoration: underline;
}

ol, ul {
    margin-top: 0;
}

li {
    margin: 10px auto;
    line-height: 25px;
}

button, input {
    outline: none;
    border-color: transparent;
    background-color: transparent;
}

hr {
    border-style: solid;
    border-width: 0.5px;
    border-color: #ebebeb;
    margin: 50px 0;
}
:root{
    --logo-size:80px;
    --logo-half-size:40px;
}

.preloader{
    display: -webkit-flex;
    display: flex;
    width: 100%;
    height: 100vh;
    background: #a1a1a1;
    position: relative;
    transition: 2s;
}

.preloader__image_animate{
    /* background-image: url(../../media/icons/pauseBtn.svg); */
    background-size: 80px 80px;
    background-size: var(--logo-size) var(--logo-size);
    width: 80px;
    width: var(--logo-size);
    height: 80px;
    height: var(--logo-size);
    position: absolute;
    top: calc(50% - 40px);
    top: calc(50% - var(--logo-half-size));
    left: calc(50% - 40px);
    left: calc(50% - var(--logo-half-size));
    transition: 1s linear;
    -webkit-animation: rotate 2s linear infinite;
            animation: rotate 2s linear infinite;
}

.preloader__image__loaderPath {
    stroke-dasharray: 150,200;
    stroke-dashoffset: -10;
    -webkit-animation: dash 1.5s ease-in-out infinite, color 6s ease-in-out infinite;
    animation: dash 2s ease-in-out infinite, color 6s ease-in-out infinite;
    stroke-linecap: round;
}

@-webkit-keyframes rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
  }

@keyframes rotate {
    100% {
        -webkit-transform: rotate(360deg);
        transform: rotate(360deg);
    }
  }

@-webkit-keyframes dash {
    0% {
      stroke-dasharray: 1,200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89,200;
      stroke-dashoffset: -35;
    }
    100% {
      stroke-dasharray: 89,200;
      stroke-dashoffset: -124;
    }
  }
  
  @keyframes dash {
    0% {
      stroke-dasharray: 1,200;
      stroke-dashoffset: 0;
    }
    50% {
      stroke-dasharray: 89,200;
      stroke-dashoffset: -35;
    }
    100% {
      stroke-dasharray: 89,200;
      stroke-dashoffset: -124;
    }
  }
